.navbar {
    padding: 0 3rem;
    background-color: rgb(33, 33, 146);
    display:  flex;
    align-items: center;
    justify-content: space-between;
    color: #fff;
    .left {
        h2 {
            font-weight: 700;
            font-size: 36px;
            padding: 0;
        }
    }
    .right {

        ul {
            display: flex;
            flex-wrap: wrap;
            li {
                list-style: none;
                margin: 0 8px;
                font-weight: 500;
                transition: 250ms ease;

                &:hover {
                    color: chocolate;
                }
            }
        }
    }
}
span{
    background: -webkit-linear-gradient(#eee, #333);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media screen and (max-width: 780px) {
    .navbar{
     .ul {
         display: flex;
         flex-direction: row;
     }
    }
    
    
 }